.game-page {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-areas:
    "playersInfo gameInfo hint"
    "playersInfo game notepad"
    "bioms game notepad"
    "legend game notepad";
  grid-template-rows: auto 1fr auto auto;
  grid-template-columns: 300px 1fr 300px;

  box-sizing: border-box;
  padding: 10px;
  grid-gap: 10px;
}

.block-wrapper {
  border: 2px solid black;
  position: relative;
  padding: 10px;
}
.block-wrapper h1 {
  background-color: #f2f3f7;
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 5px;
  transform: translateY(-60%);
  padding: 2px;
  line-height: 100%;
}

.refresh {
  cursor: pointer;
}
